import styled from 'styled-components';


export const Wrapper = styled.div`

    :root{
        --color-primary:red!important;
    }
    & .ant-drawer .drawer-title {
       
        cursor: pointer;
    }

    & .cart-btn svg{
        font-size: 20px;
        transform: rotate(-28deg);
    }
    & .cart-btn.new-pack svg,.cart-btn:hover svg{
        transform: rotate(25deg);
    }
    & .cart-btn.new-pack,.cart-btn:hover{
        transform: rotate(45deg);
        transform: scale(1.5);
        alpha: .5;
    }
    & .cart-btn svg{
        transition-property: all;
        transition-duration: .5s;
    }
    & .cart-btn{
        display: flex;
        justify-content: center;
        align-items: center;
        background:var(--color-primary)!important;
        transition-property: all;
        transition-duration: .5s;
    }
    ${({ configuration }) => {
        return `
            & .cart-btn{
                background:${configuration && configuration.primary_color || "var(--color-primary)"}!important;
                border-color:${configuration && configuration.primary_color || "var(--color-primary)"}; 
            }
            & .mydrawer .ant-drawer-title h1:hover{
                color:${configuration && configuration.primary_color || "var(--color-primary)"}!important;
            }
        `;
    }}

`;


export const Wraperdrawer = styled.div`
& .title-link a.link-reservations {

    color:#212529ed!important;
}

& .button-drawer {
    display: flex;
    justify-content: center;
}
& .btn-drawer{
    position: absolute;
    bottom: 80px;
    font-size: 20px;
    font-family: 'NoirPro';
    font-weight: 500;
    width: 40%;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 4px;
    border-radius: 13px;
}
`;