import React, { useState, useEffect } from 'react';
import { Row, Col, Drawer, Button, Menu, Icon, Avatar } from "antd";
import { Router, navigate, Link } from "@reach/router";
import { useSelector, useDispatch } from "react-redux";
import { URL_AVATAR, URL_S3 } from "../../constants"
import * as actionTypes from "../../constants/actionTypes";
import { translate } from "react-polyglot";




const { SubMenu } = Menu;
const MenuDrawer = ({ t, ...props }) => {
    const [visible, setVisible] = useState(false);
    const [open, setOpen] = useState(false);
    const locale = useSelector(({ auth }) => {
        return auth.locale;
    })
    const user = useSelector(({ auth }) => {
        return auth.user;
    });
    const dispatch = useDispatch();
    const handleClick = ({ key }) => {

        switch (key) {
            case "signout":
                localStorage.removeItem("user");
                localStorage.removeItem("feathers-jwt");
                localStorage.removeItem("cart");
                /* window.localStorage.clear(); */
                dispatch({
                    type: actionTypes.SET_CURRENT_USER,
                    user: null
                });
                dispatch({
                    type: actionTypes.RESET_STATE,
                });
                navigate("/signin");
                /*  return window.location.reload(); */
                break;
            case "es":
                dispatch({
                    type: actionTypes.CHANGE_LANGUAGE,
                    locale: "es"
                })
                break;
            case "en":
                dispatch({
                    type: actionTypes.CHANGE_LANGUAGE,
                    locale: "en"
                })
                break;
            default:
                navigate(`/${key}`);
                break;
        }
        setVisible(visible => !visible);
    };
    const showDrawer = () => {

    }
    return (
        <>

            <div>
                <Button type={!visible ? "primary" : "danger"} icon={!visible ? 'menu-unfold' : 'menu-fold'} onClick={() => setVisible(show => !show)} />
                <Drawer
                    title={<div className="section-title">
                        <div className="section-avatar">
                            <span>Menu</span>
                        </div>
                    </div>}
                    placement="left"
                    closable={false}
                    visible={visible}
                    onClose={() => {
                        setVisible(false);
                    }}
                >
                    <Menu
                        onClick={handleClick}
                        overflowedIndicator={<Icon type="menu" />}
                        className="top-menu"
                        defaultOpenKeys={['sub1']}
                        selectedKeys={[props.current]}
                        mode="inline"
                    >
                        {!user && <Menu.Item key="signin">
                            {<Menu.Item >{t("menu.signinText")}</Menu.Item>}
                        </Menu.Item>}
                        {!user && <Menu.Item key="signup">
                            {<Menu.Item >{t("menu.signupText")}</Menu.Item>}
                        </Menu.Item>}
                        {user && <SubMenu key="sub1" popupClassName="section-submenu" title={
                            <div className="user-info">
                                <Avatar
                                    src={
                                        user.avatar_path ? `${URL_S3}/${user.avatar_path}` :
                                            URL_AVATAR
                                    }
                                />{" "}
                                {<span className="user-name">{t("menu.welcomeText")}, {user.first_name || user.email.substring(0, user.email.indexOf("@"))}!</span>}
                                {/* <Tag>250pts </Tag> */}
                            </div>
                        }>
                            {<Menu.Item key="my-account">
                                <Link to={"my-account"}>{t("menu.myAccountText")}</Link>
                            </Menu.Item>}
                            {<Menu.Item key="change-password">{t("menu.changePasswordText")}</Menu.Item>}
                            {<Menu.Item key="reservations">{t("menu.myReservationsText")}</Menu.Item>}
                            {<Menu.Item key="signout">{t("menu.closeSession")}</Menu.Item>}
                            <SubMenu
                                selectedKeys={[locale]}
                                title={<span>
                                    <Icon size="large" type="global" />{" "}
                                    <span style={{
                                        fontSize: 16,
                                        fontWeight: "bold"
                                    }}>{locale.toUpperCase()}</span>
                                </span>}>
                                <Menu.Item key="es">Spanish</Menu.Item>
                                <Menu.Item key="en">English</Menu.Item>
                            </SubMenu>
                        </SubMenu>}

                        {/*   <SubMenu
                            selectedKeys={[locale]}
                            title={<span>
                                <Icon size="large" type="global" />{" "}
                                <span style={{
                                    fontSize: 16,
                                    fontWeight: "bold"
                                }}>{locale.toUpperCase()}</span>
                            </span>}>
                            <Menu.Item key="es">Spanish</Menu.Item>
                            <Menu.Item key="en">English</Menu.Item>
                        </SubMenu> */}
                    </Menu>
                </Drawer>
            </div>
        </>
    )
}
export default translate()(MenuDrawer);