import React from "react";
import styled from "styled-components";
import { H4, H5, Card } from "@bootstrap-styled/v4";
import PhoneIcon from "../components/icons/PhoneIcon";
import MailIcon from "../components/icons/MailIcon";
import Divider from "../components/Divider";
import moment from 'moment'
import { Link } from "@reach/router";
import { Layout, Row, Col, Button } from "antd";
import { translate } from "react-polyglot";
import { useSelector } from "react-redux";
const { Footer, Content } = Layout;
const backgroundFooter = require("../sources/images/osito.png");
const trianglePattern = require("../sources/images/triangle.png");
//const LogoWhite = require("../sources/images/logo-white-scp.png");

const Wrapper = styled(Footer)`
  padding: 5px 50px!important;
  background-color: var(--color-primary) !important;
  color: #fff !important;
  width: 100%;
  height:48px!important;
  bottom: 0 !important;
 
  @media(max-width:576px){
    height:auto!important;
    .footer-info{
      display: flex;
      align-items: center;
      text-align: center;
      margin: 0px auto!important;
      width: 100%;
    }
    .ant-row-flex {
      display: flex;
      justify-content:center;
      align-items: center;
    }
    .footer-image{
      width: 100%;
      margin:0px auto;
    }
  }
  /*  xl: '1200px', */
  @media(min-width:992px){
    .footer-image{
      /* height: 220px!important; */
      max-width: 400px;
      margin: 0px auto;
      float:left;
    }
    .footer-info{
      display: flex;
      float: right;
    }
  }
  /* md: '768px' */
  @media(max-width:768px){
    .footer-image{
      width: 100%!important;
      margin:0px auto!important;
      height:250px!important;
    }
    .ant-row-flex {
      display: flex;
      justify-content:center;
      align-items: center;
    }
    .footer-info{
      display: flex;
      align-items: center;
      text-align: center;
      margin: 0px auto!important;
      width: 100%;
    }
  }
  /*  lg: '992px' */
  @media(max-width:992px){
    .footer-image{
      /* height: 220px!important; */
      max-width: 400px;
      margin: 0px auto;
    }
    .footer-info{
      justify-content: center!important;
    }
    .footer-info hr{
      display:none;
    }
    .footer-content{
      width:100%!important;
      text-align: center;
      margin:10px;
     /*  box-shadow: 0px 20px 10px -15px #f5f5f56b!important; */
    }
    h4 {
      margin-top: 20px!important;
    }
    .footer-info{
      display: flex;
      align-items: center;
      text-align: center;
      margin: 0px auto!important;
      width: 100%;
    }
  }
  ${({ configuration }) => {
    return `
          background-color:${configuration && configuration.primary_color || "var(--color-primary)"}!important;
    `;
}}
`;
const Container = styled(Row)`
  min-height: 40px;
  position: relative;
  font-family: 'Jost'!important;
  & .footer-nav{
    display:flex;
    justify-content:space-between;
    align-items:center;
  }
  & .footer-nav > *{
    margin-right:10px;
    padding-right:10px;
  }
  & .footer-nav a{
    color:#FFF!important;
    font-family: 'Jost'!important;
  }
  @media(max-width:576px){
   .footer-nav a {
      font-size: 10px!important;
   }
  }
`;
const Title = styled(H4)`
  text-align: center;
  margin-bottom: 30px;
  color: #ffffff;
  opacity: 1;
  letter-spacing: 2px;
  font-size: 28px;
  margin-bottom: 20px;
  font-weight: 600;
`;
const WhiteButton = styled(Button)`
  background: #ffffff 0% 0% no-repeat padding-box !important;
  border-radius: 31px !important;
  opacity: 1 !important;
  height: 50px !important;
  width: 200px !important;
  color: var(--color-secundary) !important;
  border-color: var(--color-secundary) !important;
  font-size: var(--font-size-large) !important;
`;
const Copy = styled.span`
  letter-spacing: 0;
  color: #ffff;
  
  & strong{
    font-family:'Jost'!important;
    font-weight:bold!important;
  }
  & span{
    font-family:'Jost'!important;
  }

  a{
    color: #ffff!important;
    opacity:.75;
  }
  a:hover{
    opacity:1;
  }
  @media(max-width:576px){
    strong{
      font-size: 25px;
    }
    span{
      font-size: 17px;
    }
  }
`;

const Divi = styled(Divider)`
  height: 10.9em !important;
  margin: 20px !important;
  opacity: 1 !important;
  background-color: #ffffff !important;
`;
const MyFooter = ({ t, ...props }) => {
  const user = useSelector(({ auth }) => {
    return auth.user;
  });
  const entity = useSelector(({ auth }) => {
    return auth.entity 
  });
  const navigation = [
    {
      title: "About us",
      items: [
        { text: "Our story", to: "/" },
        { text: "Benefits", to: "/" },
        { text: "FAQ", to: "/" }
      ]
    },
    {
      title: "Legal",
      items: [
        { text: "Our story", to: "/" },
        { text: "Benefits", to: "/" },
        { text: "FAQ", to: "/" }
      ]
    },
    {
      title: "Follow us",
      items: [
        { text: "Our story", to: "/" },
        { text: "Benefits", to: "/" },
        { text: "FAQ", to: "/" }
      ]
    }
  ];
  return (
    <Wrapper configuration={{
      primary_color: entity && entity.primary_color || "var(--color-primary);",
      secundar_color: entity && entity.secundar_color || "var(--color-secundary)",
      text_color: entity && entity.text_color || "var( --color-gray-dark)"
     }}>
      <Layout
        style={{
          background: "transparent",
          margin: "0px 20px"
        }}
      >
        <Container
          type="flex"
          justify="space-between"
          align="middle"
        >
          <Col align="left">
            <div className="footer-nav">
              {user && <Link to={"my-account"}>{t("footer.myaccountLinkText")}</Link>}
              {user && <Link to={"reservations"}>{t("footer.myReservationsText")}</Link>}
              <Link to={"contact"}>{t("footer.contactsText")}</Link>
              <Link to={"faqs"}>{t("footer.faqs")}</Link>
            </div>
          </Col>
          <Col align="right">
            <Copy>
              <strong>Hotels Tours © {moment().format("YYYY")}</strong> <span>Made by <a href="https://www.donec.co" target="_blank">Donec Digital Factory</a></span>
            </Copy>
          </Col>
        </Container>
      </Layout>
    </Wrapper>
  );
};
export default translate()(MyFooter);