import React, { useEffect, useState, useRef } from "react";
import { Layout, Drawer, Button, Row, Col, Affix } from "antd";
import { styled } from "@material-ui/core";
import { TopBar, NavBar } from "../components/header";
import MyFooter from "../components/Footer";
import { useSelector } from "react-redux";
import { FiShoppingCart } from 'react-icons/fi';
import { Link, navigate } from "@reach/router";
/* Multi Idioma */
import { I18n } from 'react-polyglot';
import Cart from "../components/cart/Cart";
import { translate } from "react-polyglot";
import { Wraperdrawer,Wrapper  } from "./Styles";
import * as actionTypes from "../constants/actionTypes";
import { useDispatch } from "react-redux";



const locale = window.locale || "en";
const messages = {
  hello_name: "Hello, %{name}.",
  num_cars: "%{smart_count} car |||| %{smart_count} cars"
};

const Image = require("../");
const { Content, Header, Footer } = Layout;
const Container = styled(Content)`
  min-height: 200px !important;
`;

const HomeLayout = ({ t, id, children, ref }) => {
  const [newpack, setNewPack] = useState(false);
  const [visible, setVisible] = useState(false);
  const dispatch = useDispatch();
  const show = useSelector(({ auth }) => {
    return auth.show;
  });
  const cart = useSelector(({ auth }) => {
    return auth.cart;
  });
  const entity = useSelector(({ auth }) => {
    return auth.entity;
  });
  const user = useSelector(({ auth }) => {
    return auth.user;
  });
  const handleClick = () => {
    navigate(`/reservations/`)
    setVisible(visible => !visible)
    dispatch({
      type: actionTypes.SHOW_CAR,
      show: false
    });
  }
  const MyRef = useRef(null);
  const onClose = () => {
    dispatch({
      type: actionTypes.SHOW_CAR,
      show: false
    });
  }

  const HomeEntities = useSelector(({ auth }) => {
    return auth.HomeEntities;
  });
  useEffect(() => {
    setTimeout(() => {
      console.log(MyRef.current);
    }, 2000);
    return () => { };
  }, []);
  useEffect(() => {
    if (cart) {
      if (cart.length > 0) {
        setNewPack(true);
        let timeout = setTimeout(() => {
          setNewPack(false);
          clearTimeout(timeout);
        }, 800);
      }
      window.localStorage.setItem("cart", JSON.stringify(cart));
    }
  }, [cart]);
  return (<Wrapper configuration={{
    primary_color: entity && entity.primary_color || "var(--color-primary);",
    secundar_color: entity && entity.secundar_color || "var(--color-secundary)",
    text_color: entity && entity.text_color || "var( --color-gray-dark)",
  }}>
    <Layout>
      <NavBar />
      <Layout style={{
        minHeight: "100vh"
      }}>
        <Content style={{
          background: `url(/images/pattern.png)`,
          backgroundColor: "#FFF"
        }}>
          {children}
          <Affix
            offsetBottom={100}
            style={{
              maxHeight: 0,
              position: 'absolute',
              right: 40
            }}>
            {user && <Button
              className={`cart-btn ${newpack ? "new-pack" : ""}`}
              size="large"
              shape="circle"
              type="primary"
              onClick={() => {
                dispatch({
                  type: actionTypes.SHOW_CAR,
                  show: !show
                })
              }} >
              <FiShoppingCart />
            </Button>}
          </Affix>
        </Content>

      </Layout>
      <MyFooter />
      {<Drawer
        className={`mydrawer ${(cart.length == 0) ? "empty" : ""}`}
        width={380}
        title={
          <div className="drawer-title">
            <FiShoppingCart />{" "}
            <h1 classname="title-link" onClick={() => handleClick()}>{t("packs.cardDrawer")}</h1>
          </div>


        }
        placement="right"
        closable={true}
        mask={false}
        onClose={onClose}
        visible={show}
      ><Wraperdrawer>
          <Cart />
        </Wraperdrawer>
      </Drawer>}
    </Layout>
  </Wrapper>);
};

export default translate()(HomeLayout);
