import { navigate } from "@reach/router";
import currency from "currency-formatter";
import momentTZ from "moment";
import 'moment/locale/es';

export const momentLocale = (locale) => {
    momentTZ.locale(locale || "en");
    return momentTZ;
};


export const money = (value, decimalSeparator) => {
    return currency.format(value, {
        code: "USD",
        decimalDigits: 0,
        precision: 0,
        decimalSeparator: "."
        //typeof decimalSeparator !== "undefined" ? decimalSeparator : ","
    });
};
export const decimal = (value, decimal = ",") => {
    return currency.format(value, {
        symbol: "",
        decimal,
        precision: 0,
        format: "%v %s" // %s is the symbol and %v is the value
    });
};

export const allowAccess = (permissions, user) => {
    if (!permissions) return true;
    user = user || localStorage.getItem("user");
    if (user) {
        if (typeof user === 'string' && typeof user != "undefined")
            user = JSON.parse(user);
        if (!user.role) return false;
        console.log("USER:: ",user)
         return permissions.includes(user.role.name.toLowerCase());
    }
    return false;
};
