import React, { useEffect, useState, useRef } from "react";
import { connect, useSelector, useDispatch } from "react-redux";
import { Router, createHistory } from "@reach/router";
import styled from "styled-components";
import "antd/dist/antd.css"; // or 'antd/dist/antd.less'
import 'bootstrap/dist/css/bootstrap.css';
import "./App.css";
import { HomeLayout } from "./layouts";
/* Redux Store */
import * as actionTypes from "./constants/actionTypes"
/* Fonts */
import { library } from "@fortawesome/fontawesome-svg-core";
import { fab } from "@fortawesome/free-brands-svg-icons";
import { fas } from "@fortawesome/free-solid-svg-icons";
import { faCheckSquare, faCoffee } from "@fortawesome/free-solid-svg-icons";
/* import { GA } from "./constants" */
/* Google Analitics */
/* import ReactGA from 'react-ga'; */

import NotFound from "./views/NotFound";
import { reAuthenticate } from "./services/"
import routes from "./routes";
import { allowAccess } from "./utils";
/* Multi language */
import { I18n } from 'react-polyglot';
import messages from './language';
import Loader from "./components/loader/";

/* const locale = window.locale || "en"; */

/* ReactGA.initialize(GA);
ReactGA.pageview(window.location.pathname + window.location.search); */
library.add(fab, fas, faCheckSquare, faCoffee);
const Root = styled.div`
  width: 100%;
  min-height: 100%;
  overflow-x: hidden;
  @font-face {
     font-family: "Jost";
    /* src: url("/fonts/HKGrotesk-Medium.otf"); */
    font-weight: normal;
  }
  @font-face {
     font-family: "Jost";
    /* src: url("/fonts/HKGrotesk-Bold.otf"); */
    font-weight: bold;
  }
  html,
  h1,
  h2,
  h3,
  h4,
  h5,
  p,
  span,
  * {
    font-family: "Jost";
  }
`;
let history = createHistory(window);
const App = ({ retryLoginAndGetCurrentUser }) => {

  const [modules, setModules] = useState(routes);
  const dispatch = useDispatch();
  const locale = useSelector(({ auth }) => {
    return auth.locale;
  });
  const user = useSelector(({ auth }) => {
    return auth.user;
  });
  const data = useSelector(({ data }) => (data));
  const [initialized, setInitilized] = useState(false);
  useEffect(() => {
    reAuthenticate()
      .then(({ user }) => {
        if (user) {
          dispatch({
            type: actionTypes.CHANGE_LANGUAGE,
            locale: user.lang || locale
          })
          dispatch({
            type: actionTypes.SET_CURRENT_USER,
            user
          })
          window.localStorage.setItem("user", JSON.stringify(user));
          setInitilized(true);
        }
      })
      .catch(err => {
        setInitilized(true);
      });
  }, []);
  useEffect(() => {
    window.localStorage.setItem("locale", locale);
  }, [locale]);
  useEffect(() => {
    setModules(routes.filter(route => (allowAccess(route.permissions, user))));
  }, [user]);
  const myRef = useRef(null);
  const ScrollToTop = ({ children, location }) => {
    useEffect(
      () => window.scrollTo(0, myRef.current ? myRef.current.offsetTop : 0),
      [location.pathname]
    );
    return children;
  };
  if (!initialized) return <Loader />;
  return (<I18n locale={locale} messages={messages[locale]}>
    <Root>
      <HomeLayout>
        <Router primary={false}>
          <ScrollToTop path="/">
            {
              modules.map((route, index) => (<route.component
                key={index}
                /* loading={data.loading}
                loggedIn={data.loggedIn} */
                path={route.path}
                exact={route.exact}
                name={route.name}
                {...route}
              />
              ))
            }
            <NotFound path="/404" />
            <NotFound default />
          </ScrollToTop>
        </Router>
      </HomeLayout>
    </Root>
  </I18n>);
};

const mapStateToProps = state => {
  return {
    /* user: state.data.user */
  };
};

const mapDispatchToProps = dispatch => {
  return {
    /*  retryLoginAndGetCurrentUser: () => dispatch(retryLoginAndGetCurrentUser()) */
  };
};

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(App);

