import { Users, ChangePassword, RecoveryPasswordService, SignIn } from "./services";
import { algoliaClient } from "../api";
export const get_users = (query = {}) => Users.find(query);
export const change_password = params => ChangePassword.create(params);
export const recovery_password = params => RecoveryPasswordService.create(params);

/* Algolia Services */
const NeighborhoodIndex = algoliaClient.initIndex("neighborhoods");

export const search_neighborhoods = async query => {
    return NeighborhoodIndex.search({
        query
        /* facets: ["categories.slug"] */
    }).then(response => response.hits);
}

export * from './services'