import React, { useState, useEffect } from "react";
import styled from "styled-components";
import { useSelector, useDispatch } from "react-redux";
const Loader = styled.div`

  background-color:${({ configuration }) => {
    let entity = JSON.parse(window.localStorage.getItem("entity"));
    return entity && entity.primary_color || "var(--color-primary)"
  }};
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  z-index: 1035;

  
  box-shadow: inset 0 1px 2px rgba(0, 0, 0, 0.2);

  & .loader-track {
    position: relative;
    height: 3px;
    display: block;
    width: 100%;
    overflow: hidden;
  }

  .loader-fill:after,
  .loader-fill:before {
    content: "";
    background: #fff;
    position: absolute;

    height: 3px;
    top: 0;
    left: 0;
    bottom: 0;
    will-change: left, right;
  }

  & .loader-fill:before {
    animation: mbar 2.1s cubic-bezier(0.65, 0.815, 0.735, 0.395) infinite;
  }

  & .loader-fill:after {
    animation: m_s 2.1s cubic-bezier(0.165, 0.84, 0.44, 1) infinite;
    animation-delay: 1.15s;
  }
  /* Animation Preloader */
  @keyframes mbar {
    0% {
      left: -35%;
      right: 100%;
    }

    60% {
      left: 100%;
      right: -90%;
    }

    100% {
      left: 100%;
      right: -35%;
    }
  }
  @keyframes m_s {
    0% {
      left: -200%;
      right: 100%;
    }

    60% {
      left: 107%;
      right: -8%;
    }

    100% {
      left: 107%;
      right: -8%;
    }
  }
  
`;
const loader = ({ entity, ...props }) => {

  return (
    <Loader className="loader-bg" configuration={{
      primary_color: entity && entity.primary_color || "var(--color-primary);",
      secundar_color: entity && entity.secundar_color || "var(--color-secundary)",
      text_color: entity && entity.text_color || "var( --color-gray-dark)"
    }} >
      <div className="loader-track">
        <div className="loader-fill" />
      </div>
    </Loader>
  );
};

export default loader;
