import React from "react";
import MyFooter from "../components/Footer"
import { Layout } from "antd";


const { Content } = Layout;
const MyAccount_Layout =(props)=>{
    return( <>
     <Layout
        style={{
          maxWidth: 1200,
          overflowX: "hidden",
          margin: "0px auto"
        }}
      >
        <Layout
          style={{
            height: "100vh"
          }}
        >
          <Content></Content>
          <MyFooter/>
        </Layout>
      </Layout>
</>

    );
}
export default MyAccount_Layout;