import React, { useState, useEffect } from "react";
import { Wrapper } from "./Styles"
import { MdAddShoppingCart } from 'react-icons/md';
import { List, Col, Row, Button, Skeleton, Avatar, ConfigProvider, Icon, message, Modal } from "antd"
import { navigate } from "@reach/router"
import { isMobile } from "react-device-detect";
import { translate } from "react-polyglot";
import { useSelector, useDispatch } from "react-redux";
import * as Helper from "../../utils";
import * as actionTypes from "../../constants/actionTypes";
import { DEFAULT_IMAGE, URL_S3 } from "../../constants";
import { getService } from "../../services/";
import Loader from "../../components/loader/";
let { money } = Helper;
const { confirm } = Modal;
const Cart = ({ t, id, ...props }) => {
    const dispatch = useDispatch();
    const locale = useSelector(({ auth }) => {
        return auth.locale;
    });
    const entity = useSelector(({ auth }) => {
        return auth.entity;
    });
    const pack = useSelector(({ auth }) => {
        return auth.pack;
    });
    const user = useSelector(({ auth }) => {
        return auth.user;
    });
    const cart = useSelector(({ auth }) => {
        return auth.cart;
    });
    const [dataSource, setDataSource] = useState([]);
    const [loading, setLoading] = useState(true)
    const [submitting, setSubmitting] = useState(false);
    const handleClick = item => {
        dispatch({
            type: actionTypes.ADD_TO_CART,
            "cart": cart.filter(it => (it.id != item.id))
        })
    }
    const handleClose = () => {
        navigate(`/packs/${entity && entity.id}`);
        dispatch({
            type: actionTypes.SHOW_CAR,
            show: false
        });
    }
    const customizeRenderEmpty = () => (
        <div className="empty-text" style={{ textAlign: 'center' }}>
            <MdAddShoppingCart style={{ fontSize: 40 }} />
            <h2>{t("cart.emptyText")}</h2>
            <p>{t("cart.emptyDescriptionText")}</p>
            <Button
                onClick={handleClose}
                className="btn-add-new" type="primary" size="large" block >{t("cart.emptyButtonText")}</Button>
        </div>
    );
    const createReserve = async () => {
        const reservationService = getService("reservations");
        const reservationPackService = getService("reservations-packs");
        return new Promise(async (resolve, reject) => {
            try {
                setSubmitting(true);
                /* let reservation = await reservationService.find({
                    query: {
                        user_id: user.id,
                        entity_id: entity.id,
                        reservation_status_id: 1
                    }
                }).then(({ data }) => (data[0]));
                if (!reservation) */
                let reservation = await reservationService.create({ entity_id: entity.id });
                cart.forEach(async (pack, index) => {
                    await reservationPackService.create({
                        reservation_id: reservation.id,
                        pack_id: pack.id,
                        entity_id: entity.id,
                    });
                    if (index == cart.length - 1) {
                        setSubmitting(false);
                        resolve(reservation);
                    }
                });
            } catch (err) {
                setSubmitting(false);
                reject(err);
            }
        });
    }
    const handleReserve = async () => {
        if (!user) return confirm({
            title: t("global.confirm.Title"),
            cancelText: t("global.cancelText"),
            okText: t("signup.signinText"),
            content: t("global.confirm.Descriptions"),
            onOk() {
                return new Promise((resolve, reject) => {
                    navigate(`/signin`)
                    dispatch({
                        type: actionTypes.SHOW_CAR,
                        show: false
                    })
                    setTimeout(Math.random() > 0.5 ? resolve : reject, 1000);
                }).catch(() =>
                    console.log('Oops errors!'));
            },
            onCancel() { },
        });
        try {
            const reservation = await createReserve();
            localStorage.setItem("cart", JSON.stringify([]));
            message.success(t("notification.reserveCreated"));
            dispatch({
                type: actionTypes.ADD_TO_CART,
                cart: []
            });
            navigate(`/reservations/${reservation.id}`);

        } catch (err) {
            message.error(err.message);
        }
    }

    return (
        <Wrapper configuration={{
            primary_color: entity && entity.primary_color || "var(--color-primary);",
            secundar_color: entity && entity.secundar_color || "var(--color-secundary)",
            text_color: entity && entity.text_color || "var( --color-gray-dark)"
        }}>
            <ConfigProvider renderEmpty={customizeRenderEmpty}>
                <List
                    className="cart-list"
                    itemLayout={isMobile ? "vertical" : "horizontal"}
                    dataSource={cart}
                    renderItem={item => {
                        let cover = item.gallery ? item.gallery.gallery_media && item.gallery.gallery_media[0] &&
                            item.gallery.gallery_media[0].path ?
                            `${URL_S3}/${item.gallery.gallery_media[0].path}` : DEFAULT_IMAGE
                            : DEFAULT_IMAGE;
                        return (
                            <List.Item
                                Layout="vertical"
                                actions={[<a key="list-loadmore-more">
                                    <Button
                                        className="btn-delete"
                                        size="small"
                                        onClick={() => handleClick(item)}
                                        type="link"
                                        key="reservations-detail"
                                        shape="circle"
                                        icon="delete"
                                    />
                                </a>]}
                            >
                                <Skeleton avatar title={false} loading={item.loading} active>
                                    <List.Item.Meta
                                        avatar={
                                            <Avatar
                                                shape="square"
                                                size={70}
                                                src={cover}
                                            />
                                        }
                                        title={<div>
                                            <div className="title">
                                                <span>{item[`name_${locale}`]}</span>
                                            </div>
                                        </div>}
                                        description={<div>
                                            <div className="prices">
                                                <span>{money(item.price_cop)}</span>
                                            </div>
                                        </div>
                                        }
                                    />
                                    {/* <div>content</div> */}
                                </Skeleton>
                            </List.Item >
                        );
                    }
                    }
                    footer={<div className="cart-footer">
                        {cart.length > 0 && <Button
                            loading={submitting}
                            onClick={handleReserve}
                            className="btn-reserve"
                            type="primary"
                            block
                            size="large">{t("cart.reserveButtonText")}</Button>}
                    </div>}
                />
            </ConfigProvider>
        </Wrapper>

    );
};

export default translate()(Cart);