import styled from 'styled-components';
export const BannerfrecuentsQuestions = styled.div`
& .title-container h1 {
    display: flex;
    justify-content: center;
    align-items: center;
    font-family: 'Ruina';
    font-size: 90px;
    font-weight: 500;
    letter-spacing: -1.8px;
    color: #FFFFFF;
    opacity: 1;
    /* width: min-content; */
}

`;
export const BannerContent = styled.div`
    max-width:650px;
  
    & .lenged-container span{
        background: #4ED072 0% 0% no-repeat padding-box;
        border-radius: 20px 0px;
        opacity: 1;
        color:white;
        padding:0px 8px;
        border-color:#4ED072!important;
    }
    & .description-container h2{
        font-family:Jost!important;
        font-weight:bold!important;
        color:#FFF!important;
        text-transform:uppercase;
        font-size: 24px;

        text-align: center;
        margin-bottom: -50px;
    }
    & .title-container h1{
       
        font: Bold 50px/60px Poppins;
        letter-spacing: 0px;
        color: #FFFFFF;
        opacity: 1;

        line-height: 105px;
        font-family: Ruina!important;
        font-size: 90px;
        /* max-width: 450px; */
        text-align: center;
        font-family:Ruina!important;
        margin-bottom: 8px;
    }
    @media(max-width:576px){
      .title-container h1 {
            font-size: 40px;
            line-height: 45px;
            padding-top: 30px;
      }
     .title-container {
           display: flex;
           justify-content: center;
            }
          }
    ${({ titlePosition }) => {
        return titlePosition == "top" && `
        display: flex;
        flex-direction: column-reverse`

    }}
    /* & .banner-shadow::before {
        content: " ";
        position: absolute;
        width: 100%;
        height: 100%;
        background: red;
        left: 0;
        top: 0;
    } */
    ${({ configuration }) => {
        return `
            & .title-container h1{
                color:${configuration && configuration.text_color || "var(--color-white)"}!important;
            }
            & .description-container h2{
                color:${configuration && configuration.text_color || "var(--color-white)"}!important;
            }
        
        `;
    }}
`;
