export const ItemWrapper = styled.div`
    width: 100%!important;
    height: 500px!important;
    background-repeat: no-repeat!important;
    background-size: cover!important;
    min-height: 600px!important;
    background-position: center center!important;
    padding: 10px!important;
    display: flex;
    justify-content: center;
    align-items: center;

    /* transition: transform .4s,-webkit-transform .4s;
    &:hover{
        transform: scale(1.2);
    } */
    @media(max-width:576px){
        .title-container h1 {
            font-size: 50px;
            width: min-content;
        }
      }
`;
export const Wrapper = styled.div`
    background-color:#333333;
 
    & .ant-carousel .slick-slide {
        text-align: center;
        height: 600px!important;

    }
    @media(max-width:476px){
        .slick-track{
            width: 376px!important;
        }
        .slick-slide.slick-active.slick-current {
            width: 376px!important;
        }
        
    }
    `;
export const WrapperBanner = styled.div`
    background-color:#333333;
    
        background-position: center!important;
    
    
   
     & .ant-carousel::before{
        content:"";
        width: 100%;
        max-height:150px;
        position: absolute;
        bottom: 30px;
        background: -moz-linear-gradient(top,  rgba(0,0,0,0) 0%, rgba(0,0,0,0) 2%, rgba(0,0,0,0.65) 100%)!important; /* FF3.6-15 */
        background: -webkit-linear-gradient(top,  rgba(0,0,0,0) 0%,rgba(0,0,0,0) 2%,rgba(0,0,0,0.65) 100%)!important; /* Chrome10-25,Safari5.1-6 */
        background: linear-gradient(to bottom,  rgba(0,0,0,0) 0%,rgba(0,0,0,0) 2%,rgba(0,0,0,0.65) 100%)!important; /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
        filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#00000000', endColorstr='#a6000000',GradientType=0 )!important; /* IE6-9 */
    } 
    & .ant-carousel .slick-slide {
        text-align: center;
        height: 526px!important;
    }
     

    & .ant-col{
        width: auto;
        position: absolute;
        bottom: 32px;
        margin-left: 94px;
        text-transform: capitalize;
    }
    & .style-name {
        text-align: left;
        display: flex;
        font-size: 49px;
        font-family: 'Jost';
        font-weight: 600;
        letter-spacing: -0.98px;
        color: #FFFFFF;
        opacity: 1;
    }
    .styles-city {
        display: flex;
        text-align: left;
        font-family: 'Jost';
        font-size: 20px;
        letter-spacing: 0px;
        color: #FFFFFF;
        opacity: 1;
    }
    & .ant-avatar img {
        width: 83%;
        height: 94%;
    }
    Qmedia(max-width:375px){
        .ant-carousel::before{
           
            height:150px;
            position: absolute;
            bottom: 0px;
        }
    }
    @media(max-width:576px){
        .ant-col {
            width: auto;
            position:none!important;
            bottom: 30px!important;
            margin-left: 0px!important;
           
        }
        .style-name {
            text-align: center!important;
            font-size: 35px!important;
        }
         .ant-carousel .slick-slide {
            
        }
        .ant-carousel::before{
            content:"";
          
            position: absolute;
            bottom: 80px;
    }
    /* transition: transform .4s,-webkit-transform .4s;
    &:hover{
        transform: scale(1.2);
    } */
   
    ${({ configuration }) => {
        return `
            & .style-name {
                color:${configuration && configuration.text_color || "var(--color-white)"}!important;
            }
        
        `;
    }}
    `;