import React, { useState, useEffect } from "react";
import { Layout, Carousel, Tag } from "antd";
import { Wrapper, ItemWrapper, BannerContent } from "./Styles";
import { isMobile } from "react-device-detect";
import { translate } from "react-polyglot";
import {useSelector, useDispatch}from "react-redux";

const Item = ({ image_path, image_cover, record, titlePosition, background, thubmnail, legend, title, description, ...props }) => {
    const { name, t } = props;
    const entity = useSelector(({ auth }) => {
        return auth.entity;
    })
    return <ItemWrapper style={{
        backgroundRepeat: "no-repeat",
        backgroundSize: "cover",
        backgroundPosition: "top center!important",
        background: `url(${isMobile && thubmnail ? thubmnail : image_path})`
    }}>
        <BannerContent titlePosition={titlePosition}
            configuration={{
                primary_color: entity && entity.primary_color || "var(--color-primary);",
                secundar_color: entity && entity.secundar_color || "var(--color-secundary)",
                text_color: entity && entity.text_color || "var(--color-white)"
            }}
        >
            {description && <div className="description-container">
                <h2>{description}</h2>
            </div>}
            {title && <div className="title-container">
                <h1>{title}</h1>
            </div>}
        </BannerContent>
    </ItemWrapper>
}
const Banner = ({ t, titlePosition = "bottom", ...props }) => {
    const [dataSource, setDataSource] = useState([
    ]);
    useEffect(() => {
        if (props.dataSource)
            setDataSource(props.dataSource);
    }, [props.dataSource])
    return (<Wrapper  {...props}>
        <Carousel>
            {
                dataSource.map((item, index) => (<Item titlePosition={titlePosition} key={index} {...props} {...item} />))
            }
        </Carousel>
    </Wrapper>)
}
export default translate()(Banner);