import styled from 'styled-components';


export const Wrapper = styled.div`
font-family: 'Jost'!important;
& .skeleton{
    display:contents!important;
}
& .button-drawer {
    position: absolute;
    bottom: 90px;
}
& .title span{
    font-family: 'NoirPro'!important;
    font-size: 16px!important;
    font-weight: 400!important;
}
& .prices span{
    font-family: 'Jost'!important;
    font-size: 20px!important;
    color: var(--color-primary);
    font-weight: 600;
}
& .btn-delete {
    display: flex;
    justify-content: center;
    align-items: center;
}
& .btn-reserve {
    width: 60%!important;
    text-transform: uppercase;
    font-weight: 600;
    font-size: 18px;
}
& .cart-list .ant-spin-nested-loading{
    height: 70vh!important;
    overflow: hidden;
    overflow-y: auto;
}
& .cart-list .cart-footer {
    display: flex;
    justify-content: center;
    align-items: center;
}
& .cart-list .ant-avatar > img {
    object-fit: contain!important;
}
& .cart-list .ant-list-item {
    border-bottom: 1px solid #e8e8e8!important;
}
& .cart-list .ant-list-item:last-child {
    border-bottom: 0px!important;
}
${({ configuration }) => {
    return `
        & .btn-add-new{
            background:${configuration && configuration.primary_color || "var(--color-primary)"};
            border-color:${configuration && configuration.primary_color || "var(--color-primary)"};
        }
        & .btn-reserve{
            background:${configuration && configuration.primary_color || "var(--color-primary)"};
            border-color:${configuration && configuration.primary_color || "var(--color-primary)"};
        }
        & .prices >span{
            color:${configuration && configuration.primary_color || "var(--color-primary)"};
        }
    `;
}}
`;