import React, { useEffect, useRef } from "react";
import { Layout, Row, Col, Affix } from "antd";
import { styled } from "@material-ui/core";
import { TopBar, NavBar } from "../components/header";
import MyFooter from "../components/Footer";
import Banner from "../components/banner/Banner";
const Image = require("../");

const { Content } = Layout;
const squarePattern = require("../sources/images/square-pattern.jpg");
const Container = styled(Content)`
  background: url(${squarePattern}) !important;
  min-height: 200px !important;

  
  
`;

const MainLayout = ({ children, ref }) => {
    const MyRef = useRef(null);
    useEffect(() => {
        setTimeout(() => {
            console.log(MyRef.current);
        }, 2000);
        return () => { };
    }, []);

    return (
        <Layout style={{ minHeight: "100vh", background: "red" }}>
            <Layout>
                <NavBar />
                <Banner />
                <Container
                    style={{
                        background: `url(${squarePattern})`,
                        minHeight: 200
                    }}
                    ref={MyRef}
                >
                    {children}
                </Container>
                <MyFooter />
            </Layout>
        </Layout>
    );
};

export default MainLayout;
