import MenuDrawer from "../../components/menu-drawer/MenuDrawer"

const Search = styled(AutoComplete)`
  border-color: transparent !important;
  border-radius: 0px !important;

  border-width: 0px !important;
  border-right-width: 0px !important;
  border:0px!important;
  border-bottom: 2px solid #ccc !important;

  & .ant-input-affix-wrapper .ant-input:not(:last-child) {
    padding-right: 40px!important;
  }
  color: rgba(0, 0, 0, 0.25);
  font-size: 20px !important;
  & .ant-input,
  .ant-input:hover,
  &.ant-select-focused .ant-select-selection .ant-input:hover,
  &.ant-select-focused .ant-select-selection .ant-input:focus {
    border-right-width: 0px !important;
    box-shadow: none !important;

    border-width: 0px !important;
    border-radius: 0px !important;
    border: 0px !important;
    box-shadow: none !important;
    border-right-width: 0px !important;
  }
  &.ant-select-auto-complete.ant-select{
    border: 1px solid #ccc!important;
    border-radius: 20px!important;
    margin: 10px!important;
  }

  &.ant-select-auto-complete.ant-select .ant-select-selection{
    background: #FFF!important;
    border-radius: 30px!important;
  }
  & input, .anticon{
    color: var(--color2)!important;
  }
  & .ant-input-affix-wrapper .ant-input-suffix .anticon{
    color: #FFFF!important;
    font-size: 20px!important;
    font-weight: bold!important;
  }
  & .ant-input-affix-wrapper .ant-input-suffix {
    padding: 8px!important;
    background: var(--color-primary)!important;
    border-radius: 50%!important;
    display: flex!important;
    justify-content: center!important;
    align-items: center!important;
    font-size: 20px!important;
  }
`;
/* const { setSearchParam } = actions; */
/* const Logo = require("../../sources/images/logo.png");
const LogoSCP = require("../../sources/images/logo-scp.png"); */
const { Option, OptGroup } = AutoComplete;
const { Header, Content } = Layout;
const LogoContainer = styled.div`
  width: 200px!important;
  height: 64px;
  float: left;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0px;
  & .logo {
    background-size: 100% 100%!important;
    height: 60px !important;
    width: 100%;
    padding: 10px 0px;
    margin: 0px 10px 0px 0px;
    background-repeat: no-repeat !important;
  }
  @media(max-width:414px){
      width: 110px!important;
      padding: 0px!important;
      margin: 0px!important;
     
     
  }
`;
const Navigation = styled(Header)`
  background-color: var(--color-gray-light) !important;
  height: 64px !important;
  z-index: 1;
  width: 100%;
  padding: 2px 17px!important;
  /* box-shadow: 0px 0 10px #00000029; */

  margin-bottom:0px!important;
  margin-top: 0px!important;
  display: flex;
  align-items: center;
  & .ant-col{
    padding:0px 8px!important;
  }
  /* wil */
 
@media(max-width:414px){
  height: 50px !important;
  width: 100%;
  padding: 0px 0px!important;
  margin-bottom: 0px!important;
  margin-top: 0px!important;
}
/* ${({ configuration }) => {
    return `
  background-color:${configuration && configuration.primary_color || "var(--color-primary)"}!important;
  `;
  }} */

 
  
`;
const Divi = styled(Divider)`
  height: 1.9em !important;
  margin: 20px 3px !important;
`;
const SearchContainer = styled.div`
  display: flex;
  width: 540px;
  align-items: center;
  float: right;
  height: 100%;

  & .ant-select{
    width:100%!important;
  }
  
`;
const Toolbar = styled.div`
  display:flex;
  justify-content: space-between;
  align-items: center;
  background-color: transparent !important;

  @media(max-width:414px){
    display:flex;
    justify-content:space-between;
    align-items:center;
  }

`;
const NavBar = ({ t, children, ...props }) => {
  const [logo, setLogo] = useState(URL_LOGO);
  const entity = useSelector(({ auth }) => {
    return auth.entity;
  })
  const locale = useSelector(({ auth }) => {
    return auth.locale;
  });

  useEffect(() => {
    if (entity && entity.logo)
      setLogo(`${URL_BASE_S3}/${entity.logo}`);
  }, [entity])
  return (
    <Affix>
      <Navigation configuration={{
        primary_color: entity && entity.primary_color || "var(--color-primary);",
        secundar_color: entity && entity.secundar_color || "var(--color-secundary)",
        text_color: entity && entity.text_color || "var( --color-gray-dark)"
      }} className="nav-bar">
        <Layout
          style={{
            background: "transparent",
            margin: "0px 20px"
          }}
        >

          <Toolbar>
            <LogoContainer className="logo-container">
              <Link
                to="/"
                className="logo"
                style={{
                  background: `url(${logo})`
                }}
              />
            </LogoContainer>
            {/*  {!isMobile && <SearchContainer>
            <Search
              size="large"
              optionLabelProp="value"
              dropdownClassName="dropdown-container"
              placement="topCenter"
              dataSource={dataSource.map(
                (opt, index) => {
                  return <Option slug={opt.slug} key={opt.id} value={opt.title}>
                    <Card bordered={false}>
                      <Card.Meta
                        avatar={<Avatar size="small" src={opt.cover_picture ? `${URL_BASE_S3}${opt.cover_picture}` : DEFAULT_IMAGE} />}
                        title={opt.title}
                      >
                      </Card.Meta>
                    </Card>
                  </Option>
                }
              )} 
            onSelect={onSelect} 
               onSearch={onSearch} 
              placeholder={t("search.PlaceholderText")}
              > 
              <Input
                suffix={
                  <Icon
                    size="large"
                    type="search"
                    className="certain-category-icon"
                  />
                }
              />
            </Search>
          </SearchContainer>}*/}
            {!isMobile && <Menu /* onClik={handleOnClik} */ /* guides={guides} */ />}
            {isMobile && <MenuDrawer />}
          </Toolbar>
        </Layout>
      </Navigation>
    </Affix>
  );
};
export default translate()(NavBar);
