import React, { useEffect, useState } from "react";
import { useSelector, useDispatch } from "react-redux";
import { Menu, Icon, Divider, Avatar, Tag, Affix } from "antd";
import { URL_AVATAR, URL_S3 } from "../../constants"
import styled from "styled-components";
import { Router, Link } from "@reach/router"
import { navigate } from "@reach/router";
import * as actionTypes from "../../constants/actionTypes";
import { translate } from "react-polyglot";

const { SubMenu } = Menu;
const NavMenu = styled(Menu)`
  font-family: 'Jost';
  font-weight: bold;
  font-size: var(--font-size-medium) !important;
  line-height: 64px !important;
  float: right;
  margin: 0px !important;

  background-color: transparent !important;
  & .user-info .ant-tag{
    background: var(--color-pink-light);
    border-radius: 20px;
    border: 0px;
    padding: 4px;
    color: var(--color-primary);
    font-size: 14px;
  }
  & .user-info{
    display:flex;
    justify-content:space-between;
    align-items:center;
  }
  & .user-name{
    font-size: 14px;
  }
  & .user-info > *{
    margin-right:4px!important;
  }
  & .anticon{
    font-weight: 400!important;
  }
  & .ant-menu-submenu-title{
    padding-top: 0px!important;
    padding-bottom: 0px!important;
    padding-right: 8px!important;
    padding-left: 0px!important;
    
  }
  & .ant-menu-submenu-title i.anticon.anticon-global {
    font-size: 20px!important;
    font-weight: 100!important;
    margin: 0px!important;
    color: rgba(0,0,0,.40)!important;
  }
  & .ant-menu-submenu-title span{
    font-weight:400!important;
  }
  & .ant-menu-submenu-title i{
    vertical-align:middle!important;
  }
   &.ant-menu-horizontal {
    border-bottom: 1px solid transparent !important;
  }
  
  & .ant-menu-submenu ant-menu ant-menu-item {
    margin-bottom: 8px!important;
 }
  & .ant-menu-item:hover,
  .ant-menu-submenu,
  .ant-menu-item-active,
  .ant-menu-submenu:hover {
    border-bottom: 2px solid transparent !important;
  }
  @media(max-width:414px){
  
  }
  @media(max-width:992px){
    width:0px!important;
  }
  /* wil */

  @media(max-width:576px){
    .ant-layout-header {
      display: contents;
  }
  }
  /*  xl: '1200px', */
  @media(min-width:992px){

  }
  /* md: '768px' */
  @media(max-width:768px){

  }
  /*  lg: '992px' */
  @media(max-width:992px){

  }
`;

const MyMenu = ({ t, guides = [], ...props }) => {
  const locale = useSelector(({ auth }) => {
    return auth.locale;
  })
  const user = useSelector(({ auth }) => {
    return auth.user;
  });
  const dispatch = useDispatch();
  const handleClick = ({ key }) => {
    switch (key) {
      case "signout":
          localStorage.removeItem("user");
          localStorage.removeItem("feathers-jwt");
          localStorage.removeItem("cart");
        /* window.localStorage.clear(); */
        dispatch({
          type: actionTypes.SET_CURRENT_USER,
          user: null
        });
        dispatch({
          type: actionTypes.RESET_STATE,
        });
        navigate("/signin");
        /*  return window.location.reload(); */
        break;
      case "es":
        dispatch({
          type: actionTypes.CHANGE_LANGUAGE,
          locale: "es"
        })
        break;
      case "en":
        dispatch({
          type: actionTypes.CHANGE_LANGUAGE,
          locale: "en"
        })
        break;
      default:
        navigate(`/${key}`);
        break;
    }
  };
  return (
    <NavMenu
      overflowedIndicator={<Icon type="menu" />}
      className="top-menu"
      onClick={handleClick}
      selectedKeys={[props.current]}
      mode="horizontal"
    >
      {!user && <Menu.Item key="signin">
        {<Menu.Item >{t("menu.signinText")}</Menu.Item>}
      </Menu.Item>}
      {!user && <Menu.Item key="signup">
        {<Menu.Item >{t("menu.signupText")}</Menu.Item>}
      </Menu.Item>}
      {user && <SubMenu popupClassName="section-submenu" title={
        <div className="user-info">
          <Avatar
            src={
              user.avatar_path ? `${URL_S3}/${user.avatar_path}` :
                URL_AVATAR
            }
          />{" "}
          {<span className="user-name">{t("menu.welcomeText")}, {user.first_name || user.email.substring(0, user.email.indexOf("@"))}!</span>}
          {/* <Tag>250pts </Tag> */}
          <Icon type="down" />
        </div>
      }>
        {<Menu.Item  key="my-account">
          <Link to={"my-account"}>{t("menu.myAccountText")}</Link>
        </Menu.Item>}
        {<Menu.Item key="change-password">{t("menu.changePasswordText")}</Menu.Item>}
        {<Menu.Item key="reservations">{t("menu.myReservationsText")}</Menu.Item>}
        {<Menu.Item key="signout">{t("menu.closeSession")}</Menu.Item>}
      </SubMenu>}
      <SubMenu
        selectedKeys={[locale]}
        title={<span>
          <Icon size="large" type="global" />{" "}
          <span style={{
            fontSize: 16,
            fontWeight: "bold"
          }}>{locale.toUpperCase()}{<Icon type="down" />}</span>
        </span>}>
        <Menu.Item key="es">Spanish</Menu.Item>
        <Menu.Item key="en">English</Menu.Item>
      </SubMenu>
    </NavMenu>
  );
};
export default translate()(MyMenu);
