import { client, socket } from '../api'

export const authentication = ({ strategy = "local", email, password, ...rest }) => {
    return client.authenticate({
        strategy,
        email,
        password,
        ...rest
    });
}
/* Socket */
export const connect = ({ strategy = "jwt", accessToken, ...rest }) => {
    return socket.authenticate({
        strategy,
        accessToken,
        ...rest
    });
}
export const disconnect = socket.logout;

export const reAuthenticate = client.reAuthenticate;
export const logout = client.logout;
export const Users = client.service("users");
export const ChangePassword = client.service("change-password");
export const RecoveryPasswordService = client.service("recovery-password");
export const getService = service => client.service(service);
export const messageService = socket.service("messages");



