import {
  LOGIN,
  REGISTER,
  SET_AUTHENTICATE,
  SET_CURRENT_USER,
  START_LOADING_USER,
  END_LOADING_USER,
  CHANGE_LANGUAGE,
  SHOW_CAR,
  ADD_TO_CART,
  RESET_STATE,
  CHANGE_CONFIGURATION,
  CHANGE_ENTITY,
} from '../constants/actionTypes';

/* Defaul State */
const initialState = {
  isAuthenticate: !!window.localStorage.getItem('feathers-jwt'),
  user: window.localStorage.getItem('user') && JSON.parse(window.localStorage.getItem('user')),
  loadingUser: true,
  locale: window.localStorage.getItem("locale") || "en",
  cart: window.localStorage.getItem("cart") && JSON.parse(window.localStorage.getItem('cart')) || []
};

export default (state = initialState, { type, payload, user, ...rest }) => {
  switch (type) {
    case LOGIN:
    case REGISTER:
    case START_LOADING_USER:
      return { ...state, loadingUser: true };
    case END_LOADING_USER:
      return { ...state, loadingUser: false };
    case SET_CURRENT_USER:
      return { ...state, user };
    case SET_AUTHENTICATE:
      return { ...state, isAuthenticate: payload.value };
    case CHANGE_LANGUAGE:
      return { ...state, locale: rest.locale };
      break;
    case SHOW_CAR:
      return { ...state, show: rest.show };
      break;
    case ADD_TO_CART:
      return { ...state, cart: rest.cart };
      break;
    case CHANGE_ENTITY:
      return { ...state, entity: rest.entity };
      break;
    case CHANGE_CONFIGURATION:
      return { ...state, configuration: rest.configuration };
      break;
    case RESET_STATE:
      return initialState;
      break;
    default:
      return state;

  }
};
