import React from "react";
import Loader from "./components/loader/";
import Loadeable from "react-loadable";
/* const Main = React.lazy(() => import("./views/home/Main")); */

/* 
 <ViewPacks path="/view-packs" />
<Packs path="/views-cards" />
<Reservations path="/reservations" />
<ReservationsDetails path="/reservations/:id" />
<NotFound path="/404" />
<NotFound default />
*/
const Home = Loadeable({
    loader: () => import("./views/home/Home"),
    loading: Loader
});
const HomeEntities = Loadeable({
    loader: () => import("./views/home/HomeEntities"),
    loading: Loader
})
const Packs = Loadeable({
    loader: () => import("./views/package/Packs"),
    loading: Loader
});
const PackCategories = Loadeable({
    loader: () => import("./views/pack-categories/PackCategories"),
    loading: Loader
});
const ViewPacks = Loadeable({
    loader: () => import("./views/package/ViewPacks"),
    loading: Loader
});

const Reservations = Loadeable({
    loader: () => import("./views/reservations/Reservations"),
    loading: Loader
});

const ReservationDetails = Loadeable({
    loader: () => import("./views/reservations/ReservationsDetails"),
    loading: Loader
});
const MyAccount = Loadeable({
    loader: () => import("./views/my-account/MyAccount"),
    loading: Loader
});
const Contact = Loadeable({
    loader: () => import("./views/contact/Contact"),
    loading: Loader
});
const CityPacks = Loadeable({
    loader: () => import("./views/city-pack/CityPacks"),
    loading: Loader
});
const FrecuentSQuestions = Loadeable({
    loader: () => import("./views/frecuents-questions/FrecuentsQuestions"),
    loading: Loader
});
const Signin = Loadeable({
    loader: () => import("./views/authentication/signin/SignIn"),
    loading: Loader
});
const Signup = Loadeable({
    loader: () => import("./views/authentication/signup/Signup"),
    loading: Loader
});
const ResetPassword = Loadeable({
    loader: () => import("./views/authentication/reset-password/ResetPassword"),
    loading: Loader
});
const ChangePassword = Loadeable({
    loader: () => import("./views/authentication/change-password/ChangePassword"),
    loading: Loader
});
const ViewsAll = Loadeable({
    loader: () => import("./views/views-all/ViewsAll"),
    loading: Loader
});
const routes = [
    {
        path: "/packs/:entity_id",
        name: "Home",
        component: Home
    },
    {
        path: "/",
        name: "Home",
        component: Home
    },
    /* {
        path: "/",
        name: "HomeEntities",
        component: HomeEntities
    }, */
    {
        path: "/signin",
        name: "Iniciar Sesiónn",
        component: Signin
    },
    {
        path: "/signin/:token",
        name: "Activar Usuario",
        component: Signin
    },
    {
        path: "/signup",
        name: "Registrarse",
        component: Signup
    },
    {
        path: "/reset-password",
        name: "Recuperar Contraseña",
        component: ResetPassword
    },
    {
        path: "/change-password/:token",
        name: "Cambiar Contraseña",
        component: ChangePassword
    },
    {
        path: "/change-password",
        name: "Cambiar Contraseña",
        component: ChangePassword
    },
    {
        path: "/pack",
        name: "Packs",
        component: Packs
    },
    {
        path: "/pack/:id",
        name: "Packs",
        component: Packs
    },
    {
        path: "/view-packs",
        name: "view-packs",
        component: ViewPacks
    },
    {
        path: "/pack-categories/:id",
        name: "pack-categories",
        component: PackCategories
    },
    {
        path: "/city-packs",
        name: "paquete de ciudaades",
        component: CityPacks
    },
    {
        path: "/city-packs/:id",
        name: "city-pack-details",
        component: CityPacks
    },
    {
        path: "/view-all",
        name: "view-all-details",
        component: ViewsAll
    },
    {
        path: "/reservations",
        name: "Reservations",
        component: Reservations,
        permissions: ["admin", "superadmin", "manager", "client"]
    },
    {
        path: "/reservations/:id",
        name: "Reservation-detail",
        component: ReservationDetails,
        permissions: ["admin", "superadmin", "manager", "client"]
    },
    {
        path: "/my-account",
        name: "Mi cuenta",
        component: MyAccount,

    },
    {
        path: "/contact",
        name: "contacto",
        component: Contact
    },
    {
        path: "/faqs",
        name: "preguntas frecuentes",
        component: FrecuentSQuestions
    }
]
export default routes;